const defaultProps = {
  size: 'small',
  name: 'cloud',
};

const sizes = {
  tiny: 12,
  extraSmall: 16,
  small: 24,
  medium: 32,
  large: 48,
  extraLarge: 64,
};
const styles = {
  svg: {
    display: 'inline-block',
    verticalAlign: 'middle',
  },
  path: {
    fill: 'currentColor',
  },
};


const Icon = React.forwardRef(({ className, size, name, ...props }, ref) => (
  <svg
    ref={ref}
    {...props}
    style={{ ...styles.svg, ...props.style }}
    width={`${sizes[size]}px`}
    height={`${sizes[size]}px`}
    className={classNames(
      className && className
    )}
    viewBox="0 0 24 24"
    xmlns="http://www.w3.org/2000/svg"
    aria-hidden="true"
    focusable="false"
  >
    <path
      style={styles.path}
      d={icons[name]}
    />
  </svg>
));


Icon.displayName = 'Icon';
Icon.defaultProps = defaultProps;
Icon.propTypes = propTypes;
export default Icon;
