import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { logos } from '../../constants';

const propTypes = {
  /** The Logo visual name */
  name: PropTypes.oneOf([
    'gotit',
  ]),
  /** Scalable Vector Graphics (*.svg) or Bitmap (*.png) */
  fileType: PropTypes.oneOf([
    'svg',
    'png',
  ]),
  /** The Logo visual variant */
  variant: PropTypes.oneOf([
    'original',
    'black',
    'white',
  ]),
  /** Providing a `src` will render an `<img>` element */
  src: PropTypes.string,
  /** Providing a alt if `src` exits  */
  alt: PropTypes.string,
  /** Set the width of the logo */
  width: PropTypes.number,
  /** Set the height of the logo */
  height: PropTypes.number,
};
const defaultProps = {
  fileType: 'svg',
  variant: 'original',
  name: 'gotit',
};
const Logo = React.forwardRef(({ className, name, src, fileType, variant, alt, height, width, as: Component = 'div', ...props }, ref) => {
  let nameOri = name;
  let typeOri;
  if (nameOri) {
    typeOri = fileType || 'svg';
  }
  if (src) {
    nameOri = false;
  }
  return (
    <Component
      {...props}
      ref={ref}
      className={classNames(
        'Logo',
        'u-inlineBlock u-fontSizeNone u-lineHeightNone',
        className && className
      )}
    >
      {nameOri && (
        <img width={width} height={height} src={logos[nameOri].variants[variant][typeOri]} alt={logos[nameOri].title} className="u-maxWidthFull" />
      )}
      {src && (
        <img width={width} height={height} src={src} alt={alt} className="u-maxWidthFull" />
      )}
    </Component>
  );
});


Logo.displayName = 'Logo';
Logo.defaultProps = defaultProps;
Logo.propTypes = propTypes;
export default Logo;
